body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


.restaurant-details {
    position: relative;
}

.order-button {
    position: absolute;
    bottom: 0;
    right: 10px;
}

.restaurant figure img {
    width: 110px;
    height: 110px;
}

.restaurant-name {
    padding-bottom: 15px;
}

.restaurant-rating {
    padding-bottom: 10px;
}

.restaurant-tag {
    margin-right: 10px;
}

.restaurant-reviews {
    margin-left: 10px;
    padding-top: 4px;
    cursor: pointer;
}

.restaurant-marker {

}

.restaurant-address {
    margin-left: 5px;
}
.modal .modal-review {
    margin-top: 15px;
}

.is-large.modal-close {
    height: 100px;
    max-height: 100px;
    max-width: 100px;
    min-height: 100px;
    min-width: 100px;
    width: 100px;
}


.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.is-danger, .has-background-danger {
  background-color: #F52F41 !important;
}

.field-body {
  display: block;
}
